#navbar {
  background-color: #334756;
}

#navLink {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12pt;
  color: #f0a500;
}

#navLink:hover {
  color: #479129;
}

.wallet {
  font-size: 11pt;
  text-align: center;
  border: orange thin solid;
  border-radius: 15px;
  padding: 10px;
  transition: 0.8s;
}

.wallet:hover {
  transition: 0.3s;
  border: #55b62f thin solid;
}

.mainPic {
  width: 100%;
}

.viewAll {
  font-size: 11pt;
  text-align: center;
  border: #334756 thin solid;
  border-radius: 20px;
  margin-right: 10px;
  padding: 6px 20px 6px 20px;
  color: #334756;
  color: #f0a500;
  background-color: #334756;
  transition: 0.3s;
}

.viewAll:hover {
  transition: 0.3s;
  color: #f0a500;
  color: #334756;
  background-color: #334756;
  background-color: #f0a500;
}

#dropdownToggleCSS {
  color: #f0a500;
  background-color: #334756;
  border: none;
  box-shadow: none;
}

#dropdownToggleCSS:hover {
  transition: 0.3s;
  color: #334756;
  background-color: #f0a500;
}

#dropdownMenuCSS {
  transition: 0.3s;
}

#dropdownMenuCSS:active {
  color: #334756;
  background-color: #f0a500;
}

#dropdownMenuCSS:hover {
  transition: 0.3s;
  color: #334756;
  background-color: #f0a500;
}

.popCSS {
  transition: 0.3s;
  color: #082032;
  background-color: #bcc0c4;
  border: #bcc0c4 thin solid;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px;
}

.popCSS:hover {
  transition: 0.3s;
  background-color: #334756;
  border: #334756 thin solid;
  color: #f0a500;
  padding-inline-end: 15px;
  box-shadow: 6px 6px 0px 0px #f0a500;
}

.popImg {
  float: left;
  height: 80px;
}

.expCSS {
  transition: 0.3s;
  color: #f0a500;
  background-color: #334756;
  margin-top: 10px;
  padding: 10px;
  border: #334756 thin solid;
  border-radius: 3px;
}

.expInfo h6 {
  margin-bottom: 0px;
}

.expImg {
  border-radius: 3px;
  padding-bottom: 20px;
}

.expCSS:hover {
  transition: 0.3s;
  color: #334756;
  background-color: #f0a500;
  border: #334756 thin solid;
  box-shadow: 7px 7px 0px 0px #334756;
}

.seeMore {
  font-size: 11pt;
  text-align: center;
  border: #334756 thin solid;
  border-radius: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 40px;
  padding-right: 40px;
  color: #334756;
  transition: 0.3s;
}

.seeMore:hover {
  transition: 0.3s;
  color: #f0a500;
  background-color: #334756;
  border: #f0a500 thin solid;
}

@media (max-width: 414px) {
  .popImg {
    height: 40px;
  }
}
@media (max-width: 575px) {
  .expImg {
    width: 300px;
  }
}

@media (max-width: 992px) {
  .popImg {
    clear: right;
  }
  .popInfo {
    clear: left;
  }
}

@media (min-width: 1200px) {
  .popImg {
    height: 50px;
  }
}
